.footer {
  width: 100%;
  height: 5rem;
  background-color: var(--dark);
  color: var(--light);
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 0;
}

.continue-btn, .calc-btn,.start-btn {
  background-color: var(--primary);
  border: none;
  margin: 2rem 0;
  font-size: 1.4rem;
  border-radius: 5px;
  color: var(--light);
  width: 30rem;
  min-height: 3rem;
  transition: 0.3s ease all;
  cursor: pointer;
}

.continue-btn:hover {
  background-color: var(--primary-hover);
}

.calc-btn {
  background-color: var(--success);
}

.calc-btn:hover {
  -webkit-filter: brightness(0.9);
          filter: brightness(0.9);
}


.back-btn {
  background-color: var(--secondary);
  border: 0.5px solid var(--primary);
  color: var(--text-primary);
  margin: 2rem 0;
  font-size: 1.4rem;
  border-radius: 5px;
  width: 12rem;
  height: 3rem;
  transition: 0.3s ease all;
  cursor: pointer;
}

.back-btn:hover {
  background-color: var(--secondary-hover);
  color: var(--light);
}

.calc-again-btn {
  background-color: var(--primary);
  border: none;
  margin: 2rem 0;
  font-size: 1.4rem;
  border-radius: 5px;
  color: var(--light);
  width: 16rem;
  height: 3rem;
  transition: 0.3s ease all;
  cursor: pointer;
}

.calc-again-btn:hover {
  background-color: var(--primary-hover);
}

@media only screen and (max-width: 700px) {
  .start-btn, .continue-btn, .calc-btn {
    width: 20rem;
    margin: 0.5rem 0 2rem 0;
  }
}

@media only screen and (max-width: 484px) {
  .start-btn, .continue-btn, .calc-btn {
    width: 18rem;
    margin: 0.5rem 0 2rem 0;
  }
}

.container {
  display: flex;
  flex-direction: column;
  background-color: var(--container);
  margin: 5rem auto;
  justify-content: center;
  align-items: center;
  width: 60rem;
  height: 40rem;
  border-radius: 1rem;
  padding: 3rem;
}

.chooseColor {
  display: flex;
  margin: 2rem 0;
  grid-gap: 1rem;
  gap: 1rem;
}

@media only screen and (max-width: 1000px) {
  .container {
    width: 40rem;
  }
}

@media only screen and (max-width: 700px) {
  .container {
    width: 26rem;
    height: 30rem;
    padding: 2rem;
    justify-content: center;
    align-items: center;
  }

  h1 {
    font-size: 1.3rem;
    text-align: center;
  }

  span, p {
    text-align: center;
    font-size: 0.7rem;
  }
}

@media only screen and (max-width: 484px) {
  .container {
    width: 20rem;
    height: 30rem;
    padding: 2rem;
    justify-content: center;
    align-items: center;
  }

  h1 {
    font-size: 1.3rem;
    text-align: center;
  }

  span, p {
    text-align: center;
    font-size: 0.7rem;
  }
}

.litters {
  color: var(--success);
}


.input-container {
  display: flex;
  flex-direction: column;
  margin: 1rem 0;
}

.input-container label{
  color: var(--text-primary);
}

.input-container input {
  padding: 0.2rem 1rem;
  font-size: 1rem;
  width: 30rem;
  height: 3rem;
  border-radius: 5px;
  border: 1px solid var(--dark);
}

.input-container input:focus {
  outline: none;
  border: none;
  box-shadow: 0 0 5px 0 var(--primary);
}

@media only screen and (max-width: 700px) {
  .input-container input {
    width: 20rem;
    height: 2.5rem;
  }
}

@media only screen and (max-width: 484px) {
  .input-container input {
    width: 18rem;
    height: 2.5rem;
  }
}


